/* 视频 */
header {
  width: 100%;
  height: 12rem;
  position: relative;
}
header .head {
  width: 100%;
  height: 3rem;
  position: absolute;
  z-index: 1;
}
header .head i {
  font-size: 1.5rem;
  margin-left: 0.5rem;
  color: white;
}
