  line-height: 1.1rem;
  letter-spacing: 0.05rem;
}
.list .left p {
  font-size: 0.85rem;
  text-align: left;
  margin-left: 5%;
  color: #b0b0b0;
}
.list .right {
  width: 35%;
  height: 5rem;
}
.list .right img {
  width: 94%;
  height: 4.2rem;
  margin-top: 4.5%;
}
</style>


